// Global Variables

/////
///// Colors
/////
$color-black: #000;
$color-black-transparent-light: rgba($color-black, .1);
$color-black-transparent: rgba($color-black, .3);
$color-black-transparent-dark: rgba($color-black, .8);
$color-white: #fff;
$color-gray: #999;
$color-gray-light: lighten($color-gray, 5%);
$color-gray-lighter: lighten($color-gray-light, 5%);
$color-gray-lightest: lighten($color-gray-lighter, 5%);
$color-gray-dark: darken($color-gray, 5%);
$color-gray-darker: darken($color-gray-dark, 5%);
$color-gray-darkest: darken($color-gray-darker, 15%);
$color-blue: #3498db;
$color-blue-darker: #2874a6;

// Defining some of the common uses for the colors
$color-text: $color-gray-darkest;
$color-link: $color-blue;
$color-link-hover: $color-blue-darker;
$color-border: $color-gray;



/////
///// Font Information
/////

// TODO: Remove this filepath variable, just an example (specify a filepath for your self-hosted font here, to be used in _fonts.scss)
// $filepath-font-gotham: '../fonts/';
$font-name-helvetica: 'Helvetica';
$helvetica-normal: 400;
$helvetica-bold: 600;
$helvetica-black: 900; // sass-lint:disable-line no-color-keywords

$font-name-georgia: 'Georgia';
$georgia-normal: 400;
$georgia-bold: 900;

$font-name-primary: $font-name-helvetica;
$font-family-primary: $font-name-primary, sans-serif;
$font-weights-primary: ( 
    normal: $helvetica-normal,
    bold: $helvetica-bold,
    black: $helvetica-black // sass-lint:disable-line no-color-keywords
);

$font-name-secondary: $font-name-georgia;
$font-family-secondary: $font-name-secondary, serif;
$font-weights-secondary: ( 
    normal: $georgia-normal,
    bold: $georgia-bold
);

// https://type-scale.com/
$type-scale: (1 + (1 / 3)); // perfect fourth
$font-size-base: rem-calc(18);
$font-sizes: (
    tiny: pow($type-scale, -2) * $font-size-base,
    small: pow($type-scale, -1) * $font-size-base,
    normal: $font-size-base,
    large: pow($type-scale, 1) * $font-size-base,
    larger: pow($type-scale, 2) * $font-size-base,
    xlarge: pow($type-scale, 3) * $font-size-base,
    xxlarge: pow($type-scale, 4) * $font-size-base
);

// Different sizes depending on the breakpoint
$font-size-base-small: map-get($font-sizes, small);
$font-size-base-large: map-get($font-sizes, normal);
$font-sizes-heading: (
    small: (
        h1: map-get($font-sizes, xlarge),
        h2: map-get($font-sizes, large),
        h3: map-get($font-sizes, medium),
        h4: $font-size-base-small,
        h5: $font-size-base-small,
        h6: $font-size-base-small
    ),
    large: (
        h1: map-get($font-sizes, xxlarge),
        h2: map-get($font-sizes, xlarge),
        h3: map-get($font-sizes, larger),
        h4: $font-size-base-large,
        h5: $font-size-base-large,
        h6: $font-size-base-large
    )
);
$line-height: 1.6;
$line-height-smaller: 1.1;



/////
///// Breakpoint Info
/////
$breakpoints: (
	small: 0,
	medium: 764px,
	large: 1024px,
    xlarge: 1200px
);
$breakpoint-ends: (
	small: map-get($breakpoints, medium) - 1px,
	medium: map-get($breakpoints, large) - 1px
);



/////
///// Transition Speeds
/////
$transition-slow: .6s;
$transition-med: .3s;
$transition-fast: .1s;



/////
///// Global Z-indexes
/////
$z-util: 9999; // start at the highest
