// This partial is a little special in that it covers a bunch of different tags.  Selectors should be simple (1 or 2 deep) and only cover *tags* not classes or ids
body {
	font-size: $font-size-base-large;
}

h1 {
	font-size: map-get(map-get($font-sizes-heading, large), h1);
}

h2 {
	font-size: map-get(map-get($font-sizes-heading, large), h2);
}

h3 {
	font-size: map-get(map-get($font-sizes-heading, large), h3);
}

h4 {
	font-size: map-get(map-get($font-sizes-heading, large), h4);
}

h5 {
	font-size: map-get(map-get($font-sizes-heading, large), h5);
}

h6 {
	font-size: map-get(map-get($font-sizes-heading, large), h6);
}
